
import { API } from '../config'

export const createCategory = (userId, token, category) => {
    return fetch(`${API}/category/create/${userId}`, {
        method: "POST",
        headers: {
            Accept: 'application/json',
            Authorization: `Bearer ${token}`
        },
        body: category,
    })
        .then(response => {
            return response.json();
        })

        .catch(err => {
            console.log(err);

        })

}

