
// var loader = ;
export function loading_start(){
    document.getElementById('my-loader').style.display="block";
}
export function loading_stop(){
}


export function loading_start_bool(_is){
    if(_is){
        document.getElementById('my-loader').style.display="block";
        document.getElementById('my-loader-img').style.display="block";
    }else{
        document.getElementById('my-loader').style.display="none";
        document.getElementById('my-loader-img').style.display="none";
    }
}