import React, { useState, useEffect } from 'react'
import { isAuthenticated } from '../auth/index'
import { Link, Redirect } from 'react-router-dom'
import { read, update, updateuser, updatepassword, updateuserpassword } from '../user/apiUser'
import AdminDashboardLayout from './AdminDashboardLayout'
import { loading_start_bool } from '../js/main'


const AdminProfile = ({ match }) => {
    const [isLoading, setIsLoading] = useState(true);

    const [values, setValues] = useState({
        name: '',
        email: '',
        phone: '',
        address: {},
        pincode: '',
        error: false,
        success: false
    })

    const { name, email, phone, address, pincode } = values

    const { user, token } = isAuthenticated()

    const init = () => {
        setIsLoading(true)
        read(user._id, token).then(data => {
            if (data.error) {
                console.log(data.error);
                setIsLoading(false)
            } else {
                setValues({
                    ...values,
                    address: data.address,
                    name: data.name,
                    phone: data.phone,
                    pincode: data.pincode,
                    email: data.email,
                })
                setIsLoading(false)
            }
        })
    }


    const handleChange = name => event => {
        setValues({
            ...values,
            error: false,
            success: false,
            [name]: event.target.value
        });
    }

    const updateUserName = (event) => {
        event.preventDefault()
        setIsLoading(true)
        update(user._id, token, name).then(data => {
            if (data.error) {
                setValues({ ...values, error: data.error })
                setIsLoading(false)
            } else {
                updateuser(name, () => {
                    setValues({
                        ...values,
                        error: false,
                        name: data.name,
                        success: 'User name changed'
                    })
                    setIsLoading(false)
                })
            }
        })
    }

    const [debug, setDebug] = useState('')

    useEffect(() => {
        init()
    }, [])

    const showUsernameSuccess = () => {
        if (values.success) {
            return (
                <div className="alert alert-success mt-3">
                    {values.success}
                </div>
            )
        }
    }
    const showUsernameError = () => {
        if (values.error) {
            return (
                <div className="alert alert-danger mt-3">
                    {values.error}
                </div>
            )
        }
    }





    const profileUpdate = () => (
        <form>
            <div className="form-group">
                <label className="text-muted">Name</label>
                <input type="text" className="form-control" onChange={handleChange('name')} defaultValue={name} />
            </div>
            <div className="form-group">
                <label className="text-muted">Email</label>
                <h6>{email}</h6>
            </div>
            <div className="form-group">
                <label className="text-muted">Phone</label>
                <h6>{phone}</h6>
            </div>
            <button className="btn btn-primary" onClick={updateUserName}>Submit</button>
            {showUsernameSuccess()}
            {showUsernameError()}
        </form>
    )

    const userAddress = () => (
        <div className="mt-2">
            <hr />
            <h6 className="font-weight-bold">Address</h6>
            <h6>{address.address || ""}</h6>
            <h6>{address.city || ""}</h6>
            <h6>{address.district || ""}</h6>
            <h6>{address.state || ""}</h6>
            <h6>{pincode || ""}</h6>
            <h6>{address.land_marks || ""}</h6>
        </div>
    )


    const [password, setPassword] = useState({
        error: false,
        success: false,
        password: '',
        currentpassword: ''
    })



    const handleChangePaassword = name => event => {
        setPassword({
            ...password,
            error: false,
            success: false,
            [name]: event.target.value
        });
    }

    const handlepasswordChange = (event) => {
        event.preventDefault()
        setIsLoading(true)
        updatepassword(user._id,
            token,
            password.password, password.currentpassword)
            .then(data => {
                if (data.error) {
                    setPassword({ ...password, error: data.error })
                    setIsLoading(false)
                } else {
                    setPassword({
                        ...password,
                        error: false,
                        success: 'Password changed',
                        password: '',
                        currentpassword: ''
                    })
                    setIsLoading(false)
                }
            })
    }

    const showPasswordSuccess = () => {
        if (password.success) {
            return (
                <div className="alert alert-success mt-3 mb-2">
                    {password.success}
                </div>
            )
        }
    }
    const showPasswordError = () => {
        if (password.error) {
            return (
                <div className="alert alert-danger mt-3 mb-2">
                    {password.error}
                </div>
            )
        }
    }


    const passwordChange = () => (
        <div className="mt-2">
            <hr />
            <h6 className="font-weight-bold">Password</h6>
            {showPasswordSuccess()}
            {showPasswordError()}
            <form>
                <div className="form-group">
                    <label className="text-muted">Current Password</label>
                    <input type="password" className="form-control" onChange={handleChangePaassword('currentpassword')} placeholder="Current password" />
                </div>
                <div className="form-group">
                    <label className="text-muted">New Password</label>
                    <input type="password" className="form-control" onChange={handleChangePaassword('password')} placeholder="New password" />
                </div>
                <button className="btn btn-primary mb-3" onClick={handlepasswordChange}>Change Password</button>

            </form>
        </div>
    )

    return (
        <AdminDashboardLayout>
            {loading_start_bool(isLoading)}
            <ol class="breadcrumb mb-4 mt-4">
                <li class="breadcrumb-item"><Link to='/user/dashboard'>Dashboard</Link></li>
                <li class="breadcrumb-item active">Edit Profile</li>
            </ol>
            {profileUpdate()}
            {userAddress()}
            {passwordChange()}
        </AdminDashboardLayout>
    )
}

export default AdminProfile;