import React, { useState, useEffect } from 'react'
import { isAuthenticated } from '../auth/index'
import { Link } from 'react-router-dom'
import { createProduct, getCategories } from './apiAdmin'
import AdminDashboardLayout from './AdminDashboardLayout'
import { loading_start_bool } from '../js/main'

const AddProduct = () => {


    const [values, setValues] = useState({
        name: '',
        description: '',
        price: '',
        categories: [],
        category: '',
        shipping: '',
        quantity: '',
        photo: '',
        qty_type: '',
        pincode: '',
        offer: '',
        loading: false,
        error: '',
        createdProduct: '',
        redirectToProfile: false,
        formData: ''
    })

    const { user, token } = isAuthenticated();

    const {
        name,
        description,
        price,
        categories,
        category,
        shipping,
        qty_type,
        pincode,
        offer,
        quantity,
        loading,
        error,
        createdProduct,
        redirectToProfile,
        formData
    } = values;

    //load categories and set form data

    const init = () => {
        setValues({
            ...values,
            formData: new FormData()
        })
        getCategories().then(data => {
            if (data.error) {
                setValues({
                    ...values,
                    error: data.error
                })
            } else {
                setValues({
                    ...values,
                    categories: data,
                    formData: new FormData()
                })
            }
        })
    }

    useEffect(() => {
        init();
    }, [])


    const handleChange = name => event => {
        const value = name === 'photo' ? event.target.files[0] : event.target.value
        formData.set(name, value)
        setValues({
            ...values,
            [name]: value
        })
    }

    const clickSubmit = (event) => {
        event.preventDefault();

        setValues({
            ...values,
            error: '',
            loading: true,
        });

        createProduct(user._id, token, formData)
            .then(data => {
                if (data.error) {
                    setValues({
                        ...values,
                        error: JSON.stringify(data.error) + JSON.stringify(formData)
                    })
                } else {
                    setValues({
                        ...values,
                        name: '',
                        description: '',
                        price: '',
                        quantity: '',
                        loading: false,
                        createdProduct: data.name
                    })
                    console.log(values);

                }
            })


    }

    const newPostFrom = () => (
        <form action="" className="mb-3" onSubmit={clickSubmit}>
            <h4>Post photo</h4>
            <div className="formgroup">
                <label className="btn btn-secondary">
                    <input onChange={handleChange('photo')} type="file" name="photo" accept="image/*" />
                </label>
            </div>
            <div className="form-group">
                <label className="text-muted">Name</label>
                <input type="text" onChange={handleChange('name')} value={name} className="form-control" id="" />
            </div>
            <div className="form-group">
                <label className="text-muted">Description</label>
                <textarea onChange={handleChange('description')} value={description} className="form-control" id="" />
            </div>
            <div className="form-group">
                <label className="text-muted">Price</label>
                <input type="number" onChange={handleChange('price')} value={price} className="form-control" id="" />
            </div>
            <div className="form-group">
                <label className="text-muted">Category</label>
                <select onChange={handleChange('category')} className="form-control" >
                    <option >Plaese Select</option>
                    {
                        categories && categories.map((c, i) => (
                            <option key={i} value={c._id}>{c.name}</option>
                        ))
                    }
                </select>
            </div>

            <div className="form-group">
                <label className="text-muted">Shipping</label>
                <select onChange={handleChange('shipping')} className="form-control" >
                    <option >Plaese Select</option>
                    <option value="0">No</option>
                    <option value="1">Yes</option>
                </select>
            </div>

            <div className="form-group">
                <label className="text-muted">Quantity</label>
                <input type="number" onChange={handleChange('quantity')} value={quantity} className="form-control" id="" />
            </div>

            <div className="form-group">
                <label className="text-muted">Qty Type</label>
                <select onChange={handleChange('qty_type')} className="form-control" >
                    <option >Plaese Select</option>
                    <option value="kg">Kg</option>
                    <option value="gm">gm</option>
                    <option value="Piece">Piece</option>
                    <option value="Packet">Packet</option>
                    <option value="Bottle">Bottle</option>
                    <option value="Box">Box</option>

                </select>
            </div>

            <div className="form-group">
                <label className="text-muted">Pincode</label>
                <textarea type="number" onChange={handleChange('pincode')} value={pincode} className="form-control" id="" />
            </div>


            <div className="form-group">
                <label className="text-muted">Offer Amount <small>Set to 0 for no offer</small> </label>
                <input type="number" onChange={handleChange('offer')} value={offer} className="form-control" id="" />
            </div>

            <button className="btn btn-outline-primary">Create Product</button>

        </form>
    )

    const showError = () => (
        <div className="alert alert-danger" style={{ display: error ? '' : 'none' }}>
            {error}
        </div>
    )
    const showSucces = () => (
        <div className="alert alert-info" style={{ display: createdProduct ? '' : 'none' }}>
            <h2>{`${createdProduct} is created`}</h2>
        </div>
    )

    const showLoading = () => (
        loading_start_bool(loading)
    )

    return (
        <AdminDashboardLayout title="Add a new category" description={`Hey ${user.name} ready to add category`} >

            <div className="">
                <ol class="breadcrumb mb-4 mt-4">
                    <li class="breadcrumb-item"><Link to='/admin/dashboard'>Dashboard</Link></li>
                    <li class="breadcrumb-item active">Create Products</li>
                </ol>
                {showLoading()}
                {showSucces()}
                {showError()}
                {newPostFrom()}
            </div>

        </AdminDashboardLayout>
    )

}

export default AddProduct;