import React, { useState, useEffect, Fragment } from 'react'
import { isAuthenticated } from '../auth/index'
import { Link } from 'react-router-dom'
import { listOrdersDelcancel, getStatusValues, updateOrderStatus, getDboyList, assignDboy } from './apiAdmin'
import AdminDashboardLayout from './AdminDashboardLayout'
import Modal from 'react-bootstrap/Modal'
import { API } from '../config';
import { MDBDataTable } from 'mdbreact';
import { loading_start_bool } from '../js/main'

const OrdersDelCancel = () => {
    const [isLoading, setIsLoading] = useState(true);

    const [orders, setOrders] = useState([])
    const [statusValues, setStatusValues] = useState([])
    const { user, token } = isAuthenticated()

    const [success, setSuccess] = useState([])

    const [show, setShow] = useState(false)
    const [modalData, setModalData] = useState([])
    const handleClose = () => setShow(false);
    const handleShow = (orderProducts) => {
        setModalData(orderProducts)
        setShow(true);
    };





    const loadOrders = () => {
        listOrdersDelcancel(user._id, token)
            .then(data => {
                if (data.error) {
                    console.log((data.error));
                    setIsLoading(false)
                } else {
                    setOrders(data)
                    setIsLoading(false)
                }
            })
    }

    const loadStatusValues = () => {
        getStatusValues(user._id, token)
            .then(data => {
                if (data.error) {
                    console.log((data.error));
                } else {
                    setStatusValues(data)
                }
            })
    }

    useEffect(() => {
        loadOrders()
        loadStatusValues()
    }, [])

    const showordersLength = () => {
        if (orders.length > 0) {
            return (
                <li class="breadcrumb-item active">Total Orders: {orders.length}</li>
            )
        } else {
            return (
                <li class="breadcrumb-item active">No Orders</li>
            )
        }
    }


    const handleStatusChange = (event, orderId) => {
        setIsLoading(true)
        updateOrderStatus(user._id, token, orderId, event.target.value)
            .then(data => {
                if (data.error) {
                    console.log('status uppdate failed');
                    setIsLoading(false)

                } else {
                    loadOrders();
                    setSuccess(orderId)
                }
            })
    }

    const showStatus = (q) => (
        <div className="form-group">
            {success === q._id ? "updated" : null}
            <select onChange={(e) => handleStatusChange(e, q._id)} className="form-control">
                <option> Update Status</option>
                {statusValues.map((status, index) => (
                    <option key={index} value={status} selected={q.status === status ? true : false}>{status}</option>
                ))}
            </select>
        </div>
    )

    const productModal = () => {
        return (
            <Modal size='lg' show={show} onHide={handleClose}>
                <Modal.Header closeButton>
                    <Modal.Title>Ordered Products</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    {
                        modalData.map((item, i) => {
                            return (
                                <div className="row m-4">
                                    <div className="col-3">
                                        <img src={`${API}/products/photo/${item._id}`} alt="" srcset="" />
                                    </div>
                                    <div className="col-9">
                                        <div className="row">
                                            <h5 className="font-weight-normal">{item.name}</h5>
                                            <h6 className="text-muted">{item.description}</h6>
                                        </div>
                                        <div className="row">
                                            <div className="col-4">
                                                <p className="text-muted">{item.price}</p>
                                            </div>
                                            <div className="col-4">
                                                <p className="text-muted">Quantity: {item.count}</p>
                                            </div>
                                            <div className="col-4">
                                                <p className="text-muted">total: {item.price * item.count}</p>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            )
                        })
                    }
                </Modal.Body>
                <Modal.Footer>
                    <button className="btn btn-secondary" onClick={handleClose}>
                        Close
                    </button>
                </Modal.Footer>
            </Modal>
        )
    }

    const dataTableOrders = (orders_row) => {
        // orders_row = {...orders_row,products: 123}
        orders_row.map((p, i) => {
            orders_row[i].buttons = <Fragment>
                <button className="btn btn-sm btn-success mr-1" onClick={(e) => handleShow(orders_row[i].products)}>
                    <i class="fa fa-eye" aria-hidden="true"></i>
                </button>
            </Fragment>

            orders_row[i].orderedby = <Fragment> {p.user.name} <br /> {p.user._id.substr(p.user._id.length - 5)}</Fragment >
            //orders_row[i].orderedbyId=p.user._id
            orders_row[i].showstatus = showStatus(p)
            orders_row[i].amountinrs = "" + p.amount
            orders_row[i].smallid = p._id.substr(p._id.length - 8)
            orders_row[i].total_address = <h6>{p.address.address}<br/>
                                                {p.address.city}<br/>
                                                {p.address.district}<br/>
                                                {p.address.state}<br/> 
                                                {p.pincode}<br/>
                                                {p.address.land_marks}<br/> </h6>

        })
        const data = {
            columns: [
                {
                    label: 'Id',
                    field: 'smallid',
                    sort: 'asc',
                },
                {
                    label: 'Status',
                    field: 'showstatus',
                    sort: 'asc',
                },
                {
                    label: 'Transaction Id',
                    field: 'transaction_id',
                    sort: 'asc',
                },
                {
                    label: 'Amount',
                    field: 'amountinrs',
                    sort: 'asc',
                },
                {
                    label: 'Ordered by',
                    field: 'orderedby',
                    sort: 'asc',
                },
                {
                    label: 'Ordered On',
                    field: 'createdAt',
                    sort: 'asc',
                },
                {
                    label: 'Delivery Address',
                    field: 'total_address',
                    sort: 'asc',
                },
                {
                    label: 'Products',
                    field: 'buttons',
                    sort: 'asc',
                },
            ],
            rows: orders_row
        }

        return (
            <MDBDataTable
                btn
                striped
                bordered
                hover
                searching
                sortable
                responsive
                data={data}
            />
        );
    }


    return (
        <AdminDashboardLayout title="orders" description={`Hey ${user.name} You can manage all orders here`} >
        {loading_start_bool(isLoading)}

            <div>

                <ol class="breadcrumb mb-4 mt-4">
                    <li class="breadcrumb-item"><Link to='/admin/dashboard'>Dashboard</Link></li>
                    <li class="breadcrumb-item active">Orders</li>
                    <div className="ml-auto">
                        {showordersLength()}
                    </div>
                </ol>
                {dataTableOrders(orders)}
            </div>
            {productModal()}

        </AdminDashboardLayout>
    )
}

export default OrdersDelCancel