import React, { useState, useEffect, Fragment } from 'react'
import { isAuthenticated } from '../auth/index'
import { Link } from 'react-router-dom'
import { createCategory, getCategories, removeCategory, updateCategory } from './apiAdmin'
import AdminDashboardLayout from './AdminDashboardLayout'
import { MDBDataTable } from 'mdbreact';
import Modal from 'react-bootstrap/Modal'
import { API } from '../config'
import { loading_start_bool } from '../js/main'


const AddCategory = () => {
    const [isLoading, setIsLoading] = useState(true)
    const [values, setValues] = useState({
        name: '',
        photo: '',
        formData: ''
    })


    const {
        name,
        formData
    } = values;
    const [error, setError] = useState(false)
    const [success, setSuccess] = useState(false)

    const [categories, setCategories] = useState([])

    const [showModal, setShowModal] = useState(false)
    const [modalData, setModalData] = useState({})
    const [editCategoryInputData, setEditCategoryInputData] = useState({
        name: '',
        photo: '',
        editformData: new FormData()
    });
    const { editformData } = editCategoryInputData;
    const [editCategorySuccess, setEditCategorySuccess] = useState(false)
    const handleCloseModal = () => setShowModal(false);
    const handleShowModal = (category) => {
        setModalData(category)
        setShowModal(true);
    };


    const handleUpdateCategory = (id) => {
        updateCategory(user._id, token, id, editCategoryInputData).then(data => {
            if (data.error) {
                console.log(data.error);
            } else {
                loadCategories()
                setEditCategorySuccess(true)
                setEditCategoryInputData('')
            }
        })
    }

    const handleCategoryeditChange = name => event => {
        const value = name === 'photo' ? event.target.files[0] : event.target.value
        editformData.set(name, value)
        setEditCategoryInputData({
            ...values,
            [name]: value
        })
    }



    const handleChange = name => event => {
        const value = name === 'photo' ? event.target.files[0] : event.target.value
        formData.set(name, value)
        setValues({
            ...values,
            [name]: value
        })
    }

    const editCategoryModal = () => {
        return (
            <Modal size='lg' show={showModal} onHide={handleCloseModal}>
                <Modal.Header closeButton>
                    <Modal.Title>Edit category</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    {editCategorySuccess ? 'Category Updated' : null}

                    <div className="input-group">
                        <input type="text" className="form-control" onChange={handleCategoryeditChange('name')} defaultValue={modalData.name} name="" id="" placeholder='Category Name' autoFocus required />

                    </div>
                    <div class="input-group mt-3">
                        <div class="custom-file">
                            <input onChange={handleCategoryeditChange('photo')} type="file" class="custom-file-input" id="inputGroupFile01" />
                            <label class="custom-file-label" for="inputGroupFile01">Choose file</label>
                        </div>
                    </div>
                    <div className="row">
                        <img src={`${API}/category/photo/${modalData._id}`} className="img-thumbnail  border-0" alt="" width="50px" height="50px" />
                    </div>
                </Modal.Body>
                <Modal.Footer>
                    <button className="btn btn-secondary" onClick={handleCloseModal}>
                        Close
                    </button>
                    <button className="btn btn-primary" onClick={(e) => handleUpdateCategory(modalData._id)}>
                        Update
                    </button>
                </Modal.Footer>
            </Modal>
        )
    }

    //destruct user and info from localstorage

    const { user, token } = isAuthenticated();

    const clickSubmit = (e) => {
        e.preventDefault();
        setError('');
        setSuccess(false)
        setIsLoading(true);


        //make request to api to create category
        // setError(JSON.stringify(formData))
        createCategory(user._id, token, formData)
            .then(data => {
                if (data.error) {
                    setError(true);
                    setIsLoading(false);

                } else {
                    loadCategories()
                    setError('');
                    setSuccess(true);
                    setIsLoading(false);

                }
            })


    }


    const loadCategories = () => {
        setIsLoading(true);
        getCategories().then((data) => {
            setCategories(data)
            setIsLoading(false);
        })
    }

    const handleRemoveCategory = (catId) => {
        setIsLoading(true);
        removeCategory(user._id, token, catId).then(data => {
            if (data.error) {
                console.log(data.error);
                setIsLoading(false);
            } else {
                loadCategories()
                setIsLoading(false);
            }
        })
    }

    useEffect(() => {
        setValues({
            ...values,
            formData: new FormData()
        })

        setEditCategoryInputData({
            ...editCategoryInputData,
            editformData: new FormData()
        })
        loadCategories();
    }, [])

    const categoryTable = (categories) => {
        categories.map((c, i) => {
            categories[i].buttons = <Fragment>
                {/* <button className="btn btn-sm btn-warning mr-2" onClick={(e) => handleShowModal(c)}>
                    <i className="fa fa-edit"></i>
                </button> */}
                <button className="btn btn-sm btn-danger " onClick={(e) => handleRemoveCategory(c._id)}>
                    <i className="fa fa-ban"></i>
                </button>
            </Fragment>
            categories[i].image = <img src={`${API}/category/photo/${categories[i]._id}`} className="img-thumbnail  border-0" alt="" width="100px" height="100px" />
        })
        const data = {
            columns: [
                {
                    label: 'Id',
                    field: '_id',
                    sort: 'asc',
                },
                {
                    label: 'Name',
                    field: 'name',
                    sort: 'asc',
                },
                {
                    label: 'Image',
                    field: 'image',
                    sort: 'asc',
                },
                {
                    label: 'Controls',
                    field: 'buttons',
                    sort: 'asc',
                },
            ],
            rows: categories
        }

        return (
            <MDBDataTable
                striped
                bordered
                hover
                searching
                sortable
                data={data}
            />
        );
    }

    const newCategoryFrom = () => (
        <form onSubmit={clickSubmit} className="mb-4">
            <div className="input-group">
                <input type="text" className="form-control" onChange={handleChange('name')} value={name} name="" id="" placeholder='Category Name' autoFocus required />

            </div>
            <div class="input-group mt-3">
                <div class="custom-file">
                    <input onChange={handleChange('photo')} type="file" class="custom-file-input" id="inputGroupFile01" />
                    <label class="custom-file-label" for="inputGroupFile01">Choose file</label>
                </div>
                <span className="input-group-append"><button className="btn btn-outline-primary">Create</button></span>
            </div>
        </form>
    );

    const showSuccess = () => {
        if (success) {
            return <h3 className="text-success">{name} is created</h3>
        }
    }
    const showError = () => {
        if (error) {
            return <h3 className="text-danger">Category should be unique</h3>
        }
    }


    return (
        <AdminDashboardLayout title="Add a new category" description={`Hey ${user.name} ready to add category`} >
            {loading_start_bool(isLoading)}
            <div className="">
                <ol class="breadcrumb mb-4 mt-4">
                    <li class="breadcrumb-item"><Link to='/admin/dashboard'>Dashboard</Link></li>
                    <li class="breadcrumb-item active">Category</li>
                    <div className="ml-auto">
                    </div>
                </ol>
                {showSuccess()}
                {showError()}
                {newCategoryFrom()}
                {categoryTable(categories)}
                {editCategoryModal()}
            </div>

        </AdminDashboardLayout>
    )


};
export default AddCategory;