import React, { useState, useEffect } from 'react'
import { isAuthenticated } from '../auth/index'
import { Link } from 'react-router-dom'
import { getBanners, createBanner, deleteBanner } from './apiAdmin'
import AdminDashboardLayout from './AdminDashboardLayout'
import { MDBDataTable } from 'mdbreact';
import { API } from '../config'
import { loading_start_bool } from '../js/main'



const Banner = () => {
    const [isLoading, setIsLoading] = useState(true);

    const [banners, setBanners] = useState([]);
    const [error, setError] = useState(false)
    const [success, setSuccess] = useState(false)

    //destruct user and info from localstorage

    const { user, token } = isAuthenticated();



    const handledeleteBanner = (bannerId) => {
        setError('');
        setSuccess(true)

        //make request to api to create category
        // setError(JSON.stringify(formData))
        setIsLoading(true)
        deleteBanner(user._id, token, bannerId)
            .then(data => {
                if (data.error) {
                    setError(true);
                    setIsLoading(false)
                } else {
                    loadBanners();
                    setError('');
                    setSuccess('Banner Deleted');
                    setIsLoading(false)
                }
            })

    }


    const loadBanners = () => {
        getBanners().then(data => {
            if (data.error) {
                console.log(data.error);
                setIsLoading(false)
            } else {
                setBanners(data);
                setIsLoading(false)
            }
        })
    }
    const [values, setValues] = useState({
        name: '',
        photo: '',
        formData: ''
    })


    const {
        name,
        formData
    } = values;


    const clickSubmit = (e) => {
        e.preventDefault();
        setError('');
        setSuccess(false)
        setIsLoading(true)


        //make request to api to create category
        // setError(JSON.stringify(formData))
        createBanner(user._id, token, formData)
            .then(data => {
                if (data.error) {
                    setError(true);
                    setIsLoading(false)
                } else {
                    loadBanners();
                    setError('');
                    setSuccess('Banner Created');
                    setIsLoading(false)
                }
            })


    }




    const handleChange = name => event => {
        const value = name === 'photo' ? event.target.files[0] : event.target.value
        formData.set(name, value)
        setValues({
            ...values,
            [name]: value
        })
    }

    const newBannerForm = () => (
        <form onSubmit={clickSubmit} className="mb-4">
            <div className="input-group">
                <input type="text" className="form-control" onChange={handleChange('name')} value={name} name="" id="" placeholder='Banner Name' autoFocus required />

            </div>
            <div class="input-group mt-3">
                <div class="custom-file">
                    <input onChange={handleChange('photo')} type="file" class="custom-file-input" id="inputGroupFile01" name="photo" accept="image/*" />
                    <label class="custom-file-label" for="inputGroupFile01">Choose file</label>
                </div>
                <span className="input-group-append"><button className="btn btn-outline-primary">Create</button></span>
            </div>
        </form>
    );

    useEffect(() => {
        setValues({
            ...values,
            formData: new FormData()
        });
        loadBanners();
    }, [])

    const bannerTable = (banners) => {
        banners.map((d, i) => {
            banners[i].buttons = <button className="btn btn-sm btn-danger " onClick={(e) => handledeleteBanner(d._id)}>
                <i className="fa fa-ban"></i>
            </button>
            banners[i].photo = <img src={`${API}/banner/photo/${banners[i]._id}`} className="img-thumbnail  border-0" alt="" width="100px" height="100px" />
        })
        const data = {
            columns: [
                {
                    label: 'Id',
                    field: '_id',
                    sort: 'asc',
                },
                {
                    label: 'Name',
                    field: 'name',
                    sort: 'asc',
                },
                {
                    label: 'Photo',
                    field: 'photo',
                    sort: 'asc',
                },
                {
                    label: 'Controls',
                    field: 'buttons',
                },
            ],
            rows: banners
        }

        return (
            <MDBDataTable
                striped
                bordered
                hover
                searching
                sortable
                data={data}
            />
        );
    }


    const showSuccess = () => {
        if (success) {
            return <h3 className="text-success"> {success}</h3>
        }
    }
    const showError = () => {
        if (error) {
            return <h3 className="text-danger">Error! {error}</h3>
        }
    }


    return (
        <AdminDashboardLayout title="Add a new category" description={`Hey ${user.name} ready to add category`} >
            {loading_start_bool(isLoading)}
            <div className="">
                <ol class="breadcrumb mb-4 mt-4">
                    <li class="breadcrumb-item"><Link to='/admin/dashboard'>Dashboard</Link></li>
                    <li class="breadcrumb-item active">Banner</li>
                    <div className="ml-auto">
                    </div>
                </ol>{newBannerForm()}
                {showSuccess()}
                {showError()}
                {bannerTable(banners)}
            </div>

        </AdminDashboardLayout>
    )


};
export default Banner;