import React, { useState, useEffect } from 'react'
import { isAuthenticated } from '../auth/index'
import { Link } from 'react-router-dom'
import { getProducts, deleteProduct } from './apiAdmin'
import AdminDashboardLayout from './AdminDashboardLayout'
import { API } from '../config'
import { loading_start_bool } from '../js/main'

const ManageProducts = () => {

    const [isLoading, setIsLoading] = useState(false)

    const [products, setProducts] = useState([])

    const loadProducts = () => {
        setIsLoading(true)
        getProducts().then(data => {
            if (data.error) {
                console.log(data.error);
                setIsLoading(false)
            } else {
                setProducts(data)
                setIsLoading(false)
            }
        })
    }

    const { user, token } = isAuthenticated();

    const destroy = productId => {
        setIsLoading(true)
        deleteProduct(productId, user._id, token).then(data => {
            if (data.error) {
                console.log(data.error);
                setIsLoading(false)
            } else {
                loadProducts();

            }
        })
    }

    useEffect(() => {
        loadProducts();
    }, [])

    return (
        <AdminDashboardLayout title="Manage Products" description="Perform CRUD on products">
            {loading_start_bool(isLoading)}
            <div className="">
                <ol class="breadcrumb mb-4 mt-4">
                    <li class="breadcrumb-item"><Link to='/admin/dashboard'>Dashboard</Link></li>
                    <li class="breadcrumb-item active">Manage Products</li>
                    <li className="breadcrumb__text ml-auto">Total {products.length} products</li>
                </ol>

                {
                    products.map((p, i) => (
                        <div key={i} className="col-12 mb-1 row">
                            <div className="col-4 col-sm-1">
                                <img src={`${API}/products/photo/${p && p._id}`} className="img-thumbnail  border-0" alt="" width="50px" height="50px" />
                            </div>
                            <div className="col-3 pl-0">
                                <strong>{p.name}</strong>
                            </div>
                            <div className="col-2">
                                <p>{p.category && p.category.name}</p>
                            </div>
                            <div className="col-2">
                                <Link to={`/admin/product/update/${p._id}`}> <span className="btn btn-sm btn-warning"> <span className="fa fa-edit"></span>  </span> </Link>
                                <span onClick={() => destroy(p._id)} className="btn btn-sm btn-danger ">Delete</span>
                            </div>
                        </div>
                    ))
                }
            </div>
        </AdminDashboardLayout>
    )
}


export default ManageProducts