import React, { useState, useEffect, Fragment } from 'react'
import { isAuthenticated } from '../auth/index'
import { Link } from 'react-router-dom'
import { listOrders, getStatusValues, updateOrderStatus, getDboyList, assignDboy } from './apiAdmin'
import AdminDashboardLayout from './AdminDashboardLayout'
import Modal from 'react-bootstrap/Modal'
import { API } from '../config';
import { MDBDataTable } from 'mdbreact';
import { loading_start_bool } from '../js/main'
import print from 'print-js'

const Orders = () => {
    const [isLoading, setIsLoading] = useState(true);

    const [orders, setOrders] = useState([])
    const [statusValues, setStatusValues] = useState([])
    const { user, token } = isAuthenticated()

    const [success, setSuccess] = useState([])

    const [show, setShow] = useState(false)
    const [modalData, setModalData] = useState([])
    const handleClose = () => setShow(false);
    const handleShow = (orderProducts) => {
        setModalData(orderProducts)
        setShow(true);
    };



    const loadOrders = () => {
        listOrders(user._id, token)
            .then(data => {
                if (data.error) {
                    console.log((data.error));
                    setIsLoading(false)
                } else {
                    setOrders(data)
                    setIsLoading(false)
                }
            })
    }

    const loadStatusValues = () => {
        getStatusValues(user._id, token)
            .then(data => {
                if (data.error) {
                    console.log((data.error));
                } else {
                    setStatusValues(data)
                }
            })
    }

    useEffect(() => {
        loadOrders()
        loadStatusValues()
    }, [])

    const showordersLength = () => {
        if (orders.length > 0) {
            return (
                <li class="breadcrumb-item active">Total Orders: {orders.length}</li>
            )
        } else {
            return (
                <li class="breadcrumb-item active">No Orders</li>
            )
        }
    }


    const handleStatusChange = (event, orderId) => {
        setIsLoading(true)
        updateOrderStatus(user._id, token, orderId, event.target.value)
            .then(data => {
                if (data.error) {
                    console.log('status uppdate failed');
                    setIsLoading(false)

                } else {
                    loadOrders();
                    setSuccess(orderId)
                }
            })
    }

    const showStatus = (q) => (
        <div className="form-group">
            {success === q._id ? "updated" : null}
            <select onChange={(e) => handleStatusChange(e, q._id)} className="form-control">
                <option> Update Status</option>
                {statusValues.map((status, index) => (
                    <option key={index} value={status} selected={q.status === status ? true : false}>{status}</option>
                ))}
            </select>
        </div>
    )

    const productModal = () => {
        return (
            <Modal size='lg' show={show} onHide={handleClose}>
                <Modal.Header closeButton>
                    <Modal.Title>Ordered Products</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    {
                        modalData.map((item, i) => {
                            return (
                                <div className="row m-4">
                                    <div className="col-3">
                                        <img src={`${API}/products/photo/${item._id}`} alt="" srcset="" />
                                    </div>
                                    <div className="col-9">
                                        <div className="row">
                                            <h5 className="font-weight-normal">{item.name}</h5>
                                            <h6 className="text-muted">{item.description}</h6>
                                        </div>
                                        <div className="row">
                                            <div className="col-4">
                                                <p className="text-muted">{item.price}</p>
                                            </div>
                                            <div className="col-4">
                                                <p className="text-muted">Quantity: {item.count}</p>
                                            </div>
                                            <div className="col-4">
                                                <p className="text-muted">total: {item.price * item.count}</p>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            )
                        })
                    }
                </Modal.Body>
                <Modal.Footer>
                    <button className="btn btn-secondary" onClick={handleClose}>
                        Close
                    </button>
                </Modal.Footer>
            </Modal>
        )
    }



    const dataTableOrders = (orders_row) => {
        // orders_row = {...orders_row,products: 123}
        orders_row.map((p, i) => {

            orders_row[i].paidbyuser = <div>
                <p>{p.paid}</p><p>{p.payment_method}</p>
            </div>


            orders_row[i].orderedby = <Fragment> {p.user.name} <br /> {p.user._id.substr(p.user._id.length - 5)}</Fragment >
            //orders_row[i].orderedbyId=p.user._id
            orders_row[i].showstatus = showStatus(p)
            orders_row[i].amountinrs = "" + p.amount
            orders_row[i].smallid = p._id.substr(p._id.length - 8)
            orders_row[i].total_address = <h6>{p.address.address}<br />
                {p.address.city}<br />
                {p.address.district}<br />
                {p.address.state}<br />
                {p.pincode}<br />
                {p.address.land_marks}<br /> </h6>

            orders_row[i].buttons = <Fragment>
                <button className="btn btn-sm btn-success m-1" onClick={(e) => handleShow(orders_row[i].products)}>
                    <i class="fa fa-eye" aria-hidden="true"></i>
                </button>

                <button className="btn btn-sm btn-danger m-1" onClick={(e) => print({
                    printable: `
                   
        <p id="header">INVOICE ${orders_row[i]._id}</p>
        
        <div  style="width:45%;float:left;">
        <u>Customer Address</u><br />
                    ${p.address.address}<br />
                    ${p.address.city}<br />
                    ${p.address.district}<br />
                    ${p.address.state}<br />
                    ${p.pincode}<br />
                    ${p.address.land_marks}<br /> 
        </div>
        <div style="width:45%;float:left;">
        <u>Customer Contact</u><br />
                    ${p.user.name}<br />
                    ${p.user.phone}<br />
        </div>
		
		<table id="items" style="width:100%; margin-top:15px;">
		
		  <tr style="text-align:left;">
		      <th>Index</th>
		      <th>Name</th>
		      <th>Quantity</th>
		      <th>Type</th>
		      <th>Price (inc Offer)</th>
          </tr>
          
          ${orders_row[i].products.map((item, index) => (
                        `<tr class="item-row">
                            <td class="item-name">${index + 1}</td>
                            <td class="description"><p>${item.name}</p></td>
                            <td><p class="cost">${item.price}</p></td>
                            <td><p class="qty">${item.count} ${item.qty_type}</p></td>
                            <td><span class="price">${(item.offer!=null && item.offer>0 ?item.price-item.offer: item.price) * item.count}</span></td>
                        </tr>`
                    ))}
		  <tr>
		      <td colspan="2" class="blank"> </td>
		      <td colspan="2" class="total-line">Delivery Fee</td>

		      <td class="total-value"><p id="paid">${orders_row[i].amount<1024?'25':'0'}</p></td>
		  </tr>
		  <tr>
		      <td colspan="2" class="blank"> </td>
		      <td colspan="2" class="total-line">Total</td>
		      <td class="total-value"><div id="subtotal">${orders_row[i].amountinrs}</div></td>
		  </tr>
		  <tr>
		      <td colspan="2" class="blank"> </td>
		      <td colspan="2" class="total-line">Amount Paid</td>

		      <td class="total-value"><p id="paid">${orders_row[i].paid}</p></td>
		  </tr>
		
		</table>`,
                    type: 'raw-html',
                })} >
                    <i class="fa fa-print" aria-hidden="true"></i>
                </button>
            </Fragment >


        })
        const data = {
            columns: [
                {
                    label: 'Id',
                    field: 'smallid',
                },
                {
                    label: 'Status',
                    field: 'showstatus',
                },
                {
                    label: 'paid',
                    field: 'paidbyuser',
                },
                {
                    label: 'Amount',
                    field: 'amountinrs',
                },
                {
                    label: 'Ordered by',
                    field: 'orderedby',
                },
                {
                    label: 'Ordered On',
                    field: 'createdAt',
                },
                {
                    label: 'Delivery Address',
                    field: 'total_address',
                },
                {
                    label: 'Products',
                    field: 'buttons',
                },
            ],
            rows: orders_row
        }

        return (
            <MDBDataTable
                btn
                striped
                bordered
                hover
                searching
                sortable={true}
                responsive
                data={data}
            />
        );
    }


    return (
        <AdminDashboardLayout title="orders" description={`Hey ${user.name} You can manage all orders here`} >
            {loading_start_bool(isLoading)}

            <div>

                <ol class="breadcrumb mb-4 mt-4">
                    <li class="breadcrumb-item"><Link to='/admin/dashboard'>Dashboard</Link></li>
                    <li class="breadcrumb-item active">Orders</li>
                    <div className="ml-auto">
                        {showordersLength()}
                    </div>
                </ol>
                {dataTableOrders(orders)}
            </div>
            {productModal()}

        </AdminDashboardLayout>
    )
}

export default Orders