import React, { useState } from 'react';
import { Link,Redirect } from 'react-router-dom';
import { signUp } from '../auth/index';
import { isAuthenticated } from '../auth/index'


const Signup = () => {

    const [values, setValues] = useState({
        name: '',
        email: '',
        password: '',
        phone: '',
        error: '',
        success: false
    });


    const { name, email, password, phone, error, success } = values;

    const handleChange = name => event => {

        setValues({
            ...values,
            error: false,
            [name]: event.target.value
        });

    }



    const clickSubmit = (event) => {
        event.preventDefault();
        setValues({
            ...values,
            error: false
        })
        signUp({ name, email, phone, password })
            .then(data => {
                if (data.error) {
                    setValues({ ...values, error: data.error, success: false })
                } else {
                    setValues({
                        ...values,
                        name: '',
                        email: '',
                        password: '',
                        phone: '',
                        error: '',
                        success: true
                    })
                }
            })
    }

    const signUpform = () => (
        <form>
            <div class="form-row">
                <div class="col-md-12">
                    <div class="form-group"><label class="small mb-1" for="inputFirstName">First Name</label>
                        <input class="form-control py-4" onChange={handleChange('name')} value={name} id="inputFirstName" type="text" placeholder="Enter full name" /></div>
                </div>
            </div>
            <div class="form-group">
                <label class="small mb-1" for="inputEmailAddress">Email</label>
                <input class="form-control py-4" onChange={handleChange('email')} value={email} type="email" aria-describedby="emailHelp" placeholder="Enter email address" />
            </div>
            <div class="form-row">

                <div class="col-md-6">
                    <div class="form-group"><label class="small mb-1" for="inputFirstName">Phone</label>
                        <div className="input-group">

                            <span className="input-group-prepend"><button className="btn btn-primary">+91</button></span>
                            <input class="form-control py-4" onChange={handleChange('phone')} value={phone} type="number" placeholder="Enter Phone" /></div>

                    </div>
                </div>
                <div class="col-md-6">
                    <div class="form-group"><label class="small mb-1" for="inputPassword">Password</label>
                        <input class="form-control py-4" onChange={handleChange('password')} value={password} type="password" placeholder="Enter password" /></div>
                </div>
            </div>
            <div class="form-group mt-4 mb-0">
                <button onClick={clickSubmit} className="btn btn-primary btn-block">Create Account</button>
            </div>
        </form>
    )

    const showError = () => (
        <div className="alert alert-danger" style={{ display: error ? '' : 'none' }}>
            {error}
        </div>
    )
    const showSuccess = () => {

        return (<div className="alert alert-info" style={{ display: success ? '' : 'none' }}>
            New Account is Created. Please <Link to='/signin'>Signin</Link>
        </div>)
    }


    if(isAuthenticated()){
        return <Redirect to='/'></Redirect>
    }

    return (
        <div className="bg-primary" id="layoutAuthentication">
            <div className="nav" style={{ fontSize: '24px' }}>
                <Link className="nav-link text-white" to="/"> <i class="fas fa-angle-left"></i> Back</Link>
            </div>
            <div id="layoutAuthentication_content">
                <main>
                    <div class="container">
                        <div class="row justify-content-center">
                            <div class="col-lg-7">
                                <div class="card shadow-lg border-0 rounded-lg mt-5">
                                    <div class="card-header"><h3 class="text-center font-weight-light my-4">Join 7 MART !</h3></div>
                                    <div class="card-body">
                                        {signUpform()}
                                        <div className="mt-3">
                                            {showSuccess()}
                                            {showError()}
                                        </div>
                                    </div>
                                    <div class="card-footer text-center">
                                        <div class="small"><Link to='/signin'>Have an account? Go to login</Link></div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </main>
            </div>
        </div>
    )
};
export default Signup;