import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import { signUp } from '../auth/index';
import { isAuthenticated,makephone } from '../auth/index'
import {verifyPhone} from './apiUser'


const VerifyPhone = () => {

    const [values, setValues] = useState({
        otp: '',
        error: '',
        success: false
    });

    const {  otp, error, success } = values;
    const {user} = isAuthenticated();

    const handleChange = name => event => {

        setValues({
            ...values,
            error: false,
            [name]: event.target.value
        });

    }



    const clickSubmit = (event) => {
        event.preventDefault();
        setValues({
            ...values,
            error: false
        })
        verifyPhone(user._id,user.phone,otp)
            .then(data => {
                if (data.error) {
                    setValues({ ...values, error: data.error, success: false })
                } else {
                    
                    makephone(data.done)
                    setValues({
                        ...values,
                        error: '',
                        success: true
                    })
                }
            })
    }

    const signUpform = () => (
        <form>
            <div class="form-row">
                <div class="col-md-12">
                    <div class="form-group"><label class="small mb-1" for="inputFirstName">OTP</label>
                        <div className="input-group">

                            <input class="form-control py-4" onChange={handleChange('otp')} value={otp} type="number" aria-describedby="emailHelp" placeholder="Enter OTP" />
                        </div>
                    </div>
                </div>
            </div>
            <div class="form-group mt-4 mb-0">
                <button onClick={clickSubmit} className="btn btn-primary btn-block">Verfiy Account</button>
            </div>
        </form>
    )

    const showError = () => (
        <div className="alert alert-danger" style={{ display: error ? '' : 'none' }}>
            {error}
        </div>
    )
    const showSuccess = () => (
        <div className="alert alert-info" style={{ display: success ? '' : 'none' }}>
            Phone number Verified. Continue to <Link to='/user/dashboard'>Dashborad</Link>
        </div>
    );


    return (
        <div className="bg-primary" id="layoutAuthentication">
            <div className="nav" style={{ fontSize: '24px' }}>
                <Link className="nav-link text-white" to="/"> <i class="fas fa-angle-left"></i> Back</Link>
            </div>
            <div id="layoutAuthentication_content">
                <main>
                    <div class="container">
                        <div class="row justify-content-center">
                            <div class="col-lg-5">
                                <div class="card shadow-lg border-0 rounded-lg mt-5">
                                    <div class="card-header"><h3 class="text-center font-weight-light my-4">Verfiy Account</h3></div>
                                    <div class="card-body">
                                        {signUpform()}
                                        <div className="mt-3">
                                            {showSuccess()}
                                            {showError()}
                                        </div>
                                    </div>
                                    <div class="card-footer text-center">
                                        <div class="small"><a href="register.html">Have an account? Go to login</a></div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </main>
            </div>
        </div>
    )
};
export default VerifyPhone;