import React, { useState } from 'react';
import { Redirect, Link } from 'react-router-dom';
import { signin, authenticate, isAuthenticated } from '../auth/index';


const Signin = () => {

    const [values, setValues] = useState({
        phone: '',
        password: '',
        error: '',
        loading: false,
        redirectToReferrer: false,
    });

    const { phone, password, error, loading, redirectToReferrer } = values;
    const { user } = isAuthenticated();

    const handleChange = name => event => {
        setValues({
            ...values,
            error: false,
            [name]: event.target.value
        });

    }



    const clickSubmit = (event) => {
        event.preventDefault();
        setValues({
            ...values,
            error: false,
            loading: true,
        })
        signin({ phone, password })
            .then(data => {
                if (data.error || data.err) {
                    setValues({ ...values, error: data.error || data.err, loading: false })
                } else {
                    authenticate(data, () => {
                        setValues({
                            ...values,
                            redirectToReferrer: true,

                        })
                    })
                }
            })
    }


    const signUpform = () => (
        <form action="" onSubmit={clickSubmit}>
            <div className="form-group">
                <label htmlFor="" className="text-muted small mb-1">Phone</label>
                <div className="input-group">
                    <span className="input-group-prepend">
                        <button className="btn btn-primary">+91</button>
                    </span>
                    <input onChange={handleChange('phone')} value={phone} type="number" name="" id="" placeholder="Phone Number" className="form-control" />
                </div>
            </div>
            <div className="form-group">
                <label htmlFor="" className="text-muted small mb-1">Password</label>
                <input onChange={handleChange('password')} value={password} type="password" placeholder="Password" name="" id="" className="form-control" />
            </div>
            <div class="form-group mt-4 mb-0">
                <button onClick={clickSubmit} className="btn btn-primary btn-block">Login</button>
                </div>
        </form>
    )

    const showError = () => (
        <div className="alert alert-danger" style={{ display: error ? '' : 'none' }}>
            {error}
        </div>
    )
    const showLoading = () => (
        loading && (
            <div className="aler alert-info">
                <h2>Loading...</h2>
            </div>
        )
    );

    const redirectuser = () => {
        if (redirectToReferrer) {
            if (user && user.verified_phone === false) {
                return <Redirect to={`/user/${user._id}/verifyphone/${user.phone}`}></Redirect>
            }
            if (user && user.role === 1) {
                return <Redirect to='/admin/dashboard'></Redirect>
            } else if (user && user.role === 2) {
                return <Redirect to='/dboy/dashboard'></Redirect>
            } else {
                return <Redirect to='/user/dashboard'></Redirect>
            }
        }

        if (isAuthenticated()) {
            return <Redirect to='/'></Redirect>
        }
    }


    return (
        <div className="bg-primary" id="layoutAuthentication">
            <div className="nav" style={{ fontSize: '24px' }}>
                <Link className="nav-link text-white" to="/"> <i class="fas fa-angle-left"></i> Back</Link>
            </div>
            <div id="layoutAuthentication_content">
                <main>
                    <div class="container">
                        <div class="row justify-content-center">
                            <div class="col-lg-7">
                                <div class="card shadow-lg border-0 rounded-lg mt-5">
                                    <div class="card-header"><h3 class="text-center font-weight-light my-4">Login</h3></div>
                                    <div class="card-body">
                                        {signUpform()}
                                        <div className="mt-3">
                                            {showLoading()}
                                            {showError()}
                                            {redirectuser()}
                                        </div>
                                    </div>
                                    <div class="card-footer text-center">
                                        <div class="small"><Link to='/signup'>Create an account? Go to SignUp</Link></div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </main>
            </div>
        </div>
    )
};
export default Signin;