import { API } from '../config'



export const read = (userId, token) => {

    return fetch(`${API}/user/${userId}`, {
        method: "GET",
        headers: {
            Accept: 'application/json',
            "Content-Type": "application/json",
            Authorization: `Bearer ${token}`
        },
    })
        .then(res => {
            return res.json();
        })
        .catch(err => console.log(err));
}


export const update = (userId, token, user) => {

    return fetch(`${API}/user/${userId}`, {
        method: "PUT",
        headers: {
            Accept: 'application/json',
            "Content-Type": "application/json",
            Authorization: `Bearer ${token}`
        },
        body: JSON.stringify({'name':user})
    })
        .then(res => {
            return res.json();
        })
        .catch(err => console.log(err));
}

export const updateuser = (user, next) => {
    if (typeof window != "undefined") {
        if (localStorage.getItem('jwt')) {
            let auth = JSON.parse(localStorage.getItem('jwt'))
            auth.user.name = user
            localStorage.setItem('jwt', JSON.stringify(auth))
            next();
        }
    }
}

export const updateuseraddress = (user) => {
    if (typeof window != "undefined") {
        if (localStorage.getItem('jwt')) {
            let auth = JSON.parse(localStorage.getItem('jwt'))
            auth.user.address = user
            localStorage.setItem('jwt', JSON.stringify(auth))
        }
    }
}
export const updatepassword = (userId, token, password,currentpassword) => {
    
    return fetch(`${API}/user/password/${userId}`, {
        method: "PUT",
        headers: {
            Accept: 'application/json',
            "Content-Type": "application/json",
            Authorization: `Bearer ${token}`
        },
        body: JSON.stringify({'currentpassword':currentpassword,"password":password})
    })
        .then(res => {
            return res.json();
        })
        .catch(err => console.log(err));
}
