export const verifyPhone = (userId,phone,otp) => {

    return fetch(`${API}/user/${userId}/verifyUser/`, {
        method: "POST",
        headers: {
            Accept: 'application/json',
            "Content-Type": "application/json",
        },
        body:JSON.stringify({phone,otp})
    })
        .then(res => {
            return res.json();
        })
        .catch(err => console.log(err));
}