import React from 'react';
import { BrowserRouter, Switch, Route } from 'react-router-dom';
import Signup from './user/Signup'
import Signin from './user/Signin'
import Home from './core/Home'
import AdminRoute from './auth/AdminRoute';
import AdminDashboard from './user/AdminDashboard';
import AddCategory from './admin/AddCategory';
import AddProduct from './admin/AddProduct'
import Orders from './admin/Orders';
import ManageProducts from './admin/ManageProducts';
import UpdateProduct from './admin/UpdateProduct';
import OrdersProcessing from './admin/OrdersProcessing';
import OrdersDelCancel from './admin/OrdersDelCancel';
import Banner from './admin/Banner';
import VerifyPhone from './user/VerifyPhone';
import AdminProfile from './admin/AdminProfile';


const Routes = () => {
    return (
        <BrowserRouter>
            <Switch>
                <Route path="/signin" exact component={Signin}></Route>
                <Route path="/signup" exact component={Signup}></Route>
                <Route path="/user/:userId/verifyphone/:phone" exact component={VerifyPhone}></Route>
                <Route path="/" exact component={Home}></Route>
                <AdminRoute path="/admin/dashboard" exact component={AdminDashboard}></AdminRoute>
                <AdminRoute path="/create/category" exact component={AddCategory}></AdminRoute>
                <AdminRoute path="/create/product" exact component={AddProduct}></AdminRoute>
                <AdminRoute path="/admin/orders" exact component={Orders}></AdminRoute>
                <AdminRoute path="/admin/products" exact component={ManageProducts}></AdminRoute>
                <AdminRoute path="/admin/product/update/:productId" exact component={UpdateProduct}></AdminRoute>
                <AdminRoute path="/admin/orders-delcancel" exact component={OrdersDelCancel}></AdminRoute>
                <AdminRoute path="/admin/orders-processing" exact component={OrdersProcessing}></AdminRoute>
                <AdminRoute path="/admin/banner" exact component={Banner}></AdminRoute>
                <AdminRoute path="/admin/profile/:userId" exact component={AdminProfile}></AdminRoute>
            </Switch>
        </BrowserRouter>
    )
}


export default Routes;